<template>
  <div id="teacherManagement">
    <div class="btns">
      <router-link :to="{ path: '/addteachers' }" class="white-color el-button el-button--primary">
        <i class="el-icon-plus"></i>
        <span>新建教师</span>
      </router-link>
      <router-link :to="{ path: '/schoolRollteacherbatch' }" class="white-color el-button el-button--primary">
        <i class="el-icon-document"></i>
        <span>批量导入</span>
      </router-link>
      <el-button @click="download" type="primary" icon="upload2">下载账号</el-button>
      <el-button @click="deleteAllDialog" type="primary" :disabled="this.multipleSelection.length===0" icon="delete2" style="float: right;">批量删除</el-button>
    </div>
    <div class="search mt40">
      <el-form ref="searchParams" :model="searchParams" label-width="90px" :label-position="'left'">
        <el-row :gutter="15">
          <el-col :span="7">
            <el-input placeholder="姓名模糊筛选" v-model="searchParams.name">
              <template slot="prepend">
                <i class="el-icon-document"></i>
                <span>姓名</span>
              </template>
            </el-input>
          </el-col>
          <el-col :span="7">
            <el-input placeholder="手机号码筛选" v-model="searchParams.phone">
              <template slot="prepend">
                <i class="el-icon-document"></i>
                <span>手机</span>
              </template>
            </el-input>
          </el-col>
          <el-col :span="7" class="el-col-select">
            <el-input>
              <template slot="prepend">
                <i class="el-icon-document"></i>
                <span>职称</span>
              </template>
              <el-select v-model="searchParams.jobTitle" clearable slot="append" placeholder="请选择">
                <!--<el-option label="请选择" value=""></el-option>-->
                <el-option label="正高级" value="正高级"></el-option>
                <el-option label="高级" value="高级"></el-option>
                <el-option label="一级" value="一级"></el-option>
                <el-option label="二级" value="二级"></el-option>
                <el-option label="三级" value="三级"></el-option>
                <el-option label="无" value="无"></el-option>
              </el-select>
            </el-input>
          </el-col>
          <el-col :span="3">
            <el-button type="primary" @click="search">查询</el-button>
          </el-col>
        </el-row>
      </el-form>
      <div class="search-bg"></div>
    </div>
    <div v-if="!tableDataIsNull" v-loading="listLoading">
      <div class="teacher-table tables mt40">
        <el-table
          :data="tableData"
          border
          stripe
          style="width: 100%"
          @selection-change="handleSelectionChange">
          <el-table-column
            type="selection"
            width="55"
            align="center">
          </el-table-column>
          <el-table-column
            prop="name"
            label="姓名"
            align="center"
            width="90"
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            prop="sexStr"
            label="性别"
            align="center">
          </el-table-column>
          <el-table-column
            prop="national"
            label="民族"
            align="center">
          </el-table-column>
          <el-table-column
            prop="phone"
            label="手机号码"
            align="center"
            width="130">
          </el-table-column>
          <el-table-column
            prop="position"
            label="职务"
            align="center">
          </el-table-column>
          <el-table-column
            prop="teacherEducation"
            label="第一学历"
            align="center"
            width="130">
          </el-table-column>
          <el-table-column
            prop="jobTitle"
            label="职称"
            align="center">
          </el-table-column>
          <el-table-column
            prop="teacherSubject"
            label="任教学科"
            align="center"
            width="130"
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            label="状态"
            align="center">
            <!--<template scope="props">
                <el-button type="text" size="small" @click="showstatePopup($event, props.row.id)">{{ props.row.state }}</el-button>
            </template>-->
            <template scope="props">
              <el-dropdown
                @command="setState"
                trigger="click"
                menu-align="start">
                <span class="el-dropdown-link" style="cursor: pointer;">
                  <span>{{ stateText(props.row.state) }}</span>
                  <i class="el-icon-caret-bottom el-icon--right"></i>
                </span>
                <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item :command="props.row.id + '-1'">禁用</el-dropdown-item>
                  <el-dropdown-item :command="props.row.id + '-0'">正常</el-dropdown-item>
                  <el-dropdown-item :command="props.row.id + '-2'">兼职</el-dropdown-item>
                </el-dropdown-menu>
              </el-dropdown>
            </template>
          </el-table-column>
          <el-table-column
            label="操作"
            align="center"
            fixed="right"
            width="200">
            <template scope="props">
              <el-button type="text" size="small">
                <!--<router-link :to="{ path: '/editteachers?id='+props.row.id + '&type=detail' }" class="link-text">查看</router-link>-->
                <router-link :to="{ path: '/editteachers/detail/'+props.row.id }" class="link-text">查看</router-link>
              </el-button>
              <el-button type="text" size="small">
                <router-link :to="{ path:  '/editteachers/edit/'+props.row.id }" class="link-text">编辑</router-link>
              </el-button>
              <el-button @click="resetPasswordDialog(props.row.phone)" type="text" size="small">重置密码</el-button>
              <el-button @click="deleteOneDialog(props.row.id)" type="text" size="small">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="teacher-pagination pagination-box mt40 mb40">
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page="commonParams.pageNumber"
          :page-size="commonParams.pageSize"
          layout="prev, pager, next, jumper"
          :total="tableDataTotal">
        </el-pagination>
      </div>
      <div class="clearfix"></div>
    </div>
    <div class="teacher-nodata" v-if="tableDataIsNull">
      没有教师信息
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    name: 'teacherManagement',
    data () {
      return {
        commonParams: {
          pageNumber: 1,
          pageSize: 10,
          isSchoolHeader: 0,
          schoolId: JSON.parse(localStorage.userInfo).schools.schoolId  //11000026
        },
        searchParams: {
          name: '',
          phone: '',
          jobTitle: '',
        },
        listLoading: false, //加载列表loading图
        tableDataIsNull: false, //数据库无教师
        isFirstReqList: true,  //第一次加载列表
        tableData: [],  //列表集合
        tableDataTotal: 0,  //总数
        teacherId: 0, //当前教师id
        teacherPhone: '', //当前教师手机号
        multipleSelection: [],  //多选集合
        state: '', //用户账号状态
      }
    },
    methods:{
      //获取列表
      getList(params){
        var _this = this;
        this.listLoading = true;
        this.$ajax('schadmin/getManyTeacher', params).then(function(res) {
          _this.listLoading = false;
          if ( res.code === '200' ) {
            if ( res.data.total > 0 ) {
              _this.tableData = res.data.resultList;
              _this.tableDataTotal = res.data.total;
            } else {
              if ( _this.isFirstReqList ) {
                _this.tableDataIsNull = true;
              }
            }
            _this.isFirstReqList = false;
          } else {
            _this.$alert(res.message, '提示', {
              confirmButtonText: '确定',
              callback: () => {
                location.reload();
              }
            });
          }
        }, function(err) {})
      },
      //搜索
      search() {
        var _this = this;
        var params = Object.assign({}, this.commonParams, this.searchParams);
        this.listLoading = true;
        this.$ajax('schadmin/findTeacher', params).then(function(res) {
          _this.listLoading = false;
          _this.searchParams = {
            name: '',
            phone: '',
            jobTitle: '',
          };
          if ( res.code === '200' ) {
            if ( res.data.total > 0 ) {
              _this.tableData = res.data.resultList;
              _this.tableDataTotal = res.data.total;
            } else {
              if ( _this.isFirstReqList ) {
                _this.tableDataIsNull = true;
              }
            }
            _this.isFirstReqList = false;
          } else {
            _this.$alert(res.message, '提示', {
              type: 'warning',
              confirmButtonText: '确定',
              callback: () => {
                _this.getList(_this.commonParams);
              }
            });
          }
        }, function(err) {})
      },
      //多选
      handleSelectionChange(val) {
        this.multipleSelection = '';
        if ( val.length > 0 ) {
          for ( var i=0; i<val.length; i++ ) {
            this.multipleSelection += val[i].id + ',';
          }
          this.multipleSelection = this.multipleSelection.substring(0, this.multipleSelection.length-1);
        }
      },
      //重置密码确认框
      resetPasswordDialog(phone) {
        var _this = this;
        this.$confirm('是否要重置密码为手机号码后六位', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(function() {
          _this.teacherPhone = phone;
          _this.resetPassword();
        }).catch(function() {
          console.log('取消')
        })
      },
      //重置密码
      resetPassword() {
        var _this = this;
        var params = {phone: this.teacherPhone};
        this.$ajax('password/reset', params).then(function(res) {
          if ( res.code === '200' ) {
            _this.$message({
              message: res.message,
              type: 'success'
            });
            _this.getList(_this.commonParams);
          } else {
            _this.$message({
              message: res.message,
              type: 'warning'
            });
          }
        }, function(err) {})
      },
      //删除单个教师确认框
      deleteOneDialog(id) {
        var _this = this;
        this.$confirm('确定要删除选中教师吗', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(function() {
          _this.teacherId = id;
          _this.deleteOne();
        }).catch(function() {
          console.log('取消')
        })
      },
      //删除
      deleteOne() {
        var _this = this;
        var params = Object.assign({}, {teacherId: this.teacherId});
        this.$ajax('schadmin/deleteOneTeacher', params).then(function(res) {
          if ( res.code === '200' ) {
            _this.$message({
              message: res.message,
              type: 'success'
            });
            _this.getList(_this.commonParams);
          } else {
            _this.$alert(res.message, '提示', {
              confirmButtonText: '确定',
              callback: () => {
                _this.getList(_this.commonParams);
              }
            });
          }
        }, function(err) {})
      },
      //批量删除教师确认框
      deleteAllDialog() {
        var _this = this;
        this.$confirm('确定要删除选中教师吗', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(function() {
          _this.deleteAll();
        }).catch(function() {
          console.log('取消')
        })
      },
      //批量删除
      deleteAll() {
        var _this = this;
        var params = {ids: this.multipleSelection.toString()};
        this.$ajax('schadmin/batchDeleteTeacher', params).then(function(res) {
          if ( res.code === '200' ) {
            _this.$message({
              message: res.message,
              type: 'success'
            });
            _this.multipleSelection = [];
            _this.getList(_this.commonParams);
          } else {
            _this.$alert(res.message, '提示', {
              confirmButtonText: '确定',
              callback: () => {
                _this.getList(_this.commonParams);
              }
            });
          }
        }, function(err) {})
      },
      //下载账号
      download() {
        alert('下载账号暂未提供接口')
      },
      //展示状态文字
      stateText: function(state) {
        var stateText = '';
        switch ( state ) {
          case 0:
            stateText = '正常';
            break;
          case 1:
            stateText = '禁用';
            break;
          case 2:
            stateText = '兼职';
            break;
        }
        return stateText;
      },
      //设置状态
      setState(command) {
        var _this = this;
        var params = Object.assign({}, {id: command.split('-')[0], state: command.split('-')[1]});
        this.$ajax('schadmin/changeState', params).then(function(res) {
          if ( res.code === '200' ) {
//            this.stateStyle.show = false;
            _this.$message({
              message: res.message,
              type: 'success'
            });
            _this.getList(_this.commonParams);
          } else {
            _this.$alert(res.message, '提示', {
              confirmButtonText: '确定',
              callback: () => {
                _this.getList(_this.commonParams);
              }
            });
          }
        }, function(err) {})
      },
      //跳页
      handleCurrentChange(val) {
        console.log(val);
        this.commonParams.pageNumber = val;
        this.getList(this.commonParams);
      }
    },
    beforeMount(){
      this.getList(this.commonParams)
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  #teacherManagement { padding: 20px; min-height: 400px;}
  #teacherManagement .white-color { color: #fff;}
  #teacherManagement .link-text { color: #20a0ff;}
  #teacherManagement .mt20 { margin-top: 20px;}
  #teacherManagement .mt40 { margin-top: 40px;}
  #teacherManagement .mb40 { margin-bottom: 40px;}
  #teacherManagement .btns { border-bottom: 1px solid #ECEDEF; padding-bottom: 15px; padding-top: 15px;}
  #teacherManagement .teacher-nodata { background: url("../../../resources/front/imgs/nodata.png") no-repeat center; text-align: center; padding-top: 100px; min-height: 300px;}
  #teacherManagement .search { padding: 20px 20px 20px 80px; border: 1px solid #cedaea; border-top:3px solid #0d9ee9; margin-top: 18px; position: relative;}
  #teacherManagement .search-bg { position: absolute; left: 12px; top: -11px; z-index: 10; width: 56px; height: 61px; background: url("../../../resources/front/imgs/search-bg.png") no-repeat;}
  #teacherManagement .pagination-box { float: right;}
  #teacherManagement .state { position: absolute; z-index: 99999; width: 100px; left: -9999px; top: -9999px;}
  #teacherManagement .state .state-btn { height: 26px; line-height: 26px; border: 1px solid #d1dbe5; border-bottom: none; background: #fff; font-size: 14px;  cursor: pointer; color:#20a0ff; text-align: center;}
  #teacherManagement .state .state-btn:hover { background: #eef1f6;}
  #teacherManagement .state .state-btn:last-child { border-bottom: 1px solid #d1dbe5;}
  #teacherManagement .triangle-left{width: 0; height: 0; border-top: 10px solid transparent; border-left: 20px solid #d1dbe5; border-bottom: 10px solid transparent; position: absolute; z-index: 100000; top: 4px; left: -27px;}
  #teacherManagement .el-col-select .el-input-group__append { width: 190px; border-left: 1px solid #BFCBD9;}
  #teacherManagement .el-col-select .el-input__inner { padding: 0; text-indent: 12px; border: none;}
</style>



